import * as React from 'react';
import { CalendarIcon } from '../../assets/svg';
import { useScheduleContext } from '../../context/scheduleContext';
import { useResultContext } from '../../context/resultContext';

type THistoryComponent = {
  list_stages: any;
  applicant_id: string;
};

export default function HistoryComponent({ list_stages, applicant_id }: THistoryComponent) {
  const { dispatch } = useScheduleContext();
  const { dispatch: dispatchResult } = useResultContext();

  const filteredStages = list_stages.filter((stage) => stage.count_schedule > 0 || stage.has_result_assessment === 1);

  /**
   * History
   * Menggunakan context untuk popup dan param
   */
  const handleOpenDetailHistory = async (applicant_id: string, vacancy_stage_id: string) => {
    dispatch({ type: 'OPEN_DETAIL_SCHEDULE', payload: { isOpen: true, applicant_id, vacancy_stage_id } });
  };

  /**
   * Result
   */
  const handleOpenPopupResult = (vacancy_stage_id: string, stage_name: string) => {
    dispatchResult({ type: 'OPEN_DETAIL_RESULT', payload: { isOpen: true, applicant_id, vacancy_stage_id } });
  };

  return (
    <React.Fragment>
      <div className="pb-4 space-y-3">
        <h3 className="text-gray-500 font-semibold">History Rekrutmen</h3>

        <div className="px-5 py-[15px] bg-gray-100 text-gray-600 rounded-xl flex justify-between">
          {filteredStages.length > 0 ? (
            <React.Fragment>
              {filteredStages.map((stage: any) => {
                return (
                  <React.Fragment key={stage.vacancy_stage_id}>
                    <div className="flex items-center space-x-2">
                      <CalendarIcon />
                      <p className="text-xs">{stage.stage_name}</p>
                    </div>
                    <div className="space-x-2">
                      <button
                        onClick={() => handleOpenPopupResult(stage.vacancy_stage_id, '')}
                        className="bg-white px-[10px] py-[5px] rounded-full border border-blue-700 text-blue-700 active:scale-90 duration-150 text-xs hover:border-blue-700/50"
                      >
                        View Result
                      </button>

                      <button
                        onClick={() => handleOpenDetailHistory(applicant_id, stage.vacancy_stage_id)}
                        className="bg-white px-[10px] py-[5px] rounded-full border border-blue-700 text-blue-700 active:scale-90 duration-150 text-xs hover:border-blue-700/50"
                      >
                        View Detail
                      </button>
                    </div>
                  </React.Fragment>
                );
              })}
            </React.Fragment>
          ) : (
            <NoData />
          )}
        </div>
      </div>
    </React.Fragment>
  );
}

function NoData() {
  return <div className="text-sm">No Data</div>;
}
